<!--
 * @Author: Marlon
 * @Date: 2020-02-24 09:03:21
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-25 12:28:12
 * @Description: 数据采集-采集任务管理-任务列表
 -->
 
<template>
  <div class="task-list">
    <div class="task-list-edit-area">
      <ul class="task-list-edit-area-btn">
        <li>
          <el-button type="primary" size="medium" @click="showDialog({type:'新增任务'})">新增任务</el-button>
        </li>
      </ul>
      <ul class="task-list-edit-area-select">
        <li>
          <label for>项目：</label>
          <el-select
            v-model="selectVal"
            filterable
            placeholder="请选择"
            size="small"
            @change="selectChange"
          >
            <el-option
              v-for="item in selectOpt"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
      </ul>
    </div>
    <div class="task-list-table">
      <TableDefault
        :TLoading="TLoading"
        :TTitle="tableTitle"
        :TData="tableData"
        :TEdit="TEdit"
        :TIsShowPage="true"
        :TTotal="TPage.total"
        :TCurrentPage="TPage.index"
        :TPageSize="TPage.size"
        @editBtnCb="editBtnCb"
        @clickCell="lickCell"
        @changePage="changePage"
      ></TableDefault>
    </div>
    <!-- 弹出框 -->
    <FormDialog
      :FDVisible="FDVisible"
      :FDTitle="FDTitle"
      FDWidth="500"
      :FDForm="FDForm"
      :FDFoot="FDFoot"
      @editBtnCb="editBtnCb"
    ></FormDialog>
    <ColumnDialog
      :ColDialogVisible="ColDialogVisible"
      :ColDialogConfig="ColDialogConfig"
      @editBtnCb="editBtnCb"
    ></ColumnDialog>
  </div>
</template>

<script>
import mixin from "mixin";
import ColumnDialog from "@/components/dialog/column-dialog";
import { openUrl } from "utils/jump";
export default {
  mixins: [mixin],
  components: {
    ColumnDialog
  },
  data() {
    return {
      TLoading: false,
      // 编辑-表格配置
      TEdit: {
        show: true,
        title: "操作",
        width: 450,
        editTypes: [
          {
            label: "编辑",
            css: ""
          },
          {
            label: "删除",
            css: "",
            pass: true
          },
          {
            label: "设置数据源",
            css: ""
          },
          {
            label: "编辑转换",
            css: "",
            link: "/dataCollection/task/conf",
            jump: {
              type: "int", // out为外部跳转，否则为内部
              mode: "push" // 根据不同的type有不同的参数
            }
          },
          {
            label: "数据统计",
            css: "",
            link: "/other/dataStatistics",
            jump: {
              type: "int", // out为外部跳转，否则为内部
              mode: "push" // 根据不同的type有不同的参数
            }
          }
        ]
      },
      tableTitle: [
        {
          prop: "name",
          label: "名称"
        },
        {
          prop: "date",
          label: "数据来源"
        },
        {
          prop: "address",
          label: "备注"
        }
      ],
      tableData: [
        {
          date: "122",
          name: "通用",
          address: "45"
        },
        {
          date: "122",
          name: "海淀",
          address: "45"
        },
        {
          date: "122",
          name: "山西",
          address: "45"
        },
        {
          date: "122",
          name: "济南",
          address: "45"
        },
        {
          date: "122",
          name: "山东",
          address: "45"
        },
        {
          date: "122",
          name: "贵州",
          address: "45"
        },
        {
          date: "122",
          name: "福州",
          address: "45"
        },
        {
          date: "122",
          name: "泰安",
          address: "45"
        },
        {
          date: "122",
          name: "南京",
          address: "45"
        },
        {
          date: "122",
          name: "广州",
          address: "45"
        }
      ],
      TPage: {
        total: 100,
        index: 1,
        size: 10
      },
      // 是否显示弹出框
      FDVisible: false,
      FDTitle: "",
      // 弹出框配置
      FDForm: [
        {
          type: "select",
          label: {
            name: "所属项目",
            width: "120"
          },
          width: "200",
          defaultModel: "",
          placeholder: "",
          option: [
            {
              label: "海淀",
              value: "haidian"
            }
          ]
        },
        {
          type: "input",
          label: {
            name: "中文名称",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "input",
          label: {
            name: "库名",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "textarea",
          label: {
            name: "备注",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: "",
          autosize: {
            min: 2,
            max: 4
          }
        }
      ],
      FDFoot: {
        button: [
          {
            type: "ok",
            label: "确定",
            cb: form => {
              switch (this.dialogActiveItem) {
                case "新增任务":
                  // TODO
                  console.log("新增任务请求");
                  return;
                case "编辑":
                  console.log("编辑请求");
                  return;

                default:
                  break;
              }
            }
          }
        ]
      },
      // 可跳转的值
      TClickItem: [
        {
          link: "/",
          cells: ["address"]
        }
      ],
      // 下拉选择
      selectOpt: [
        {
          value: "选项1",
          label: "所有项目"
        },
        {
          value: "选项2",
          label: "通用"
        },
        {
          value: "选项3",
          label: "南京"
        },
        {
          value: "选项4",
          label: "山东"
        },
        {
          value: "选项5",
          label: "济南"
        }
      ],
      selectVal: "选项1",
      dialogActiveItem: "",
      // 弹出框配置
      ColDialogConfig: null,
      ColDialogVisible: false
    };
  },
  methods: {
    editBtnCb(editItem, tid, row) {
      // console.log(editItem);
      let text = editItem.label;
      if (text === "删除") {
        // TODO
        console.log("删除请求");
      } else if (text === "close") {
        this.FDVisible = editItem.close;
        this.ColDialogVisible = editItem.close;
      }
    },
    // 显示弹出框并修改配置
    showDialog({ type, initDig = null }) {
      this.dialogActiveItem = type;
      switch (type) {
        case "新增任务":
          this.FDTitle = "新增任务";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "项目",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: this.selectOpt
            },
            {
              type: "input",
              label: {
                name: "编码",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "input",
              label: {
                name: "名称",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "textarea",
              label: {
                name: "备注",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: "",
              autosize: {
                min: 2,
                max: 4
              }
            }
          ];
          return;
        case "编辑":
          this.FDTitle = "编辑任务信息";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "项目",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: this.selectOpt
            },
            {
              type: "input",
              label: {
                name: "编码",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "input",
              label: {
                name: "名称",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "textarea",
              label: {
                name: "备注",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: "",
              autosize: {
                min: 2,
                max: 4
              }
            }
          ];
          // TODO 获取下拉数据并初始化赋值
          if (initDig && JSON.stringify(initDig) !== "{}") {
            for (const key in initDig) {
              if (initDig.hasOwnProperty(key)) {
                // TODO 优化
                this.FDForm.forEach(item => {
                  if (key === item.key) {
                    item["key"] = key;
                    item["defaultModel"] = initDig[key];
                  }
                });
              }
            }
          }
          // http
          return;
        default:
          return;
      }
    },
    showColDialog({ type, initDig = null }) {
      switch (type) {
        case "设置数据源":
          this.ColDialogConfig = {
            title: "设置数据源",
            width: 600,
            header: {
              show: true,
              data: [
                {
                  type: "button",
                  class: "",
                  label: "添加数据源",
                  classType: "primary",
                  cb: () => {
                    // TODO 跳转到数据源管理项目
                    // openUrl({type:'',url:'',obj:{}})
                  }
                },
                {
                  type: "button",
                  class: "info",
                  label: "参照源一键生成目标库",
                  classType: "info",
                  cb: () => {
                    // TODO 跳转到数据源管理项目
                    // openUrl({type:'',url:'',obj:{}})
                  }
                }
              ]
            },
            connect: {
              readyOnly: false,
              data: [
                {
                  label: {
                    name: "数据来源",
                    width: 100
                  },
                  list: [
                    {
                      type: "select",
                      defaultVal: "",
                      placeholder: "请选择数据源类型",
                      modelKey: "",
                      option: [
                        {
                          value: "选项1",
                          label: "黄金糕"
                        },
                        {
                          value: "选项2",
                          label: "双皮奶"
                        },
                        {
                          value: "选项3",
                          label: "蚵仔煎"
                        },
                        {
                          value: "选项4",
                          label: "龙须面"
                        },
                        {
                          value: "选项5",
                          label: "北京烤鸭"
                        }
                      ]
                    },
                    {
                      type: "select",
                      defaultVal: "",
                      placeholder: "",
                      modelKey: "",
                      option: [
                        {
                          value: "选项1",
                          label: "黄金糕"
                        },
                        {
                          value: "选项2",
                          label: "双皮奶"
                        },
                        {
                          value: "选项3",
                          label: "蚵仔煎"
                        },
                        {
                          value: "选项4",
                          label: "龙须面"
                        },
                        {
                          value: "选项5",
                          label: "北京烤鸭"
                        }
                      ]
                    }
                  ]
                },
                {
                  label: {
                    name: "数据去向",
                    width: 100
                  },
                  list: [
                    {
                      type: "select",
                      defaultVal: "",
                      placeholder: "请选择数据源类型",
                      modelKey: "",
                      option: [
                        {
                          value: "选项1",
                          label: "黄金糕"
                        },
                        {
                          value: "选项2",
                          label: "双皮奶"
                        },
                        {
                          value: "选项3",
                          label: "蚵仔煎"
                        },
                        {
                          value: "选项4",
                          label: "龙须面"
                        },
                        {
                          value: "选项5",
                          label: "北京烤鸭"
                        }
                      ]
                    },
                    {
                      type: "select",
                      defaultVal: "",
                      placeholder: "",
                      modelKey: "",
                      option: [
                        {
                          value: "选项1",
                          label: "黄金糕"
                        },
                        {
                          value: "选项2",
                          label: "双皮奶"
                        },
                        {
                          value: "选项3",
                          label: "蚵仔煎"
                        },
                        {
                          value: "选项4",
                          label: "龙须面"
                        },
                        {
                          value: "选项5",
                          label: "北京烤鸭"
                        }
                      ]
                    }
                  ]
                }
              ]
            },
            foot: {
              show: true,
              data: [
                {
                  type: "button",
                  class: "",
                  label: "确定",
                  classType: "primary",
                  cb: ""
                }
              ]
            }
          };
          this.ColDialogVisible = true;
          // TODO 初始化赋值
          break;

        default:
          break;
      }
    },
    // 分页操作
    changePage({ pageSize, currentPage }) {
      this.$set(this.TPage, `index`, currentPage);
      this.$set(this.TPage, `size`, pageSize);
      // this.DSMCList();
      // TODO
    },
    // 单元格事件
    lickCell({ row, column, id }) {
      const { datasourceName, remark } = row;
      switch (column.label) {
        case "编辑":
          this.showDialog({
            type: column.label,
            initDig: {
              datasourceName,
              remark,
              id: row.id
            }
          });
          return;
        case "删除":
          this.$confirm(`即将删除“${datasourceName}”任务, 是否继续?`, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.del({ id: row.id });
            })
            .catch(() => {});
          return;
        case "设置数据源":
          this.showColDialog({
            type: column.label,
            initDig: {
              datasourceName,
              remark
            }
          });
          return;
        default:
          break;
      }
    },
    // 选择下拉
    selectChange(item) {
      // TODO 发请求
      console.log(item);
    },

    /* 请求 */
    del() {
      console.log("删除请求");
    }
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
.task-list {
  &-edit-area {
    overflow: hidden;
    &-btn {
      float: left;
      overflow: hidden;
      li {
        float: left;
        margin: 2px 15px;
      }
    }
    &-select {
      float: left;
      overflow: hidden;
      margin: 4px 55px;
    }
  }
  &-table {
    margin: 20px 0;
  }
}
</style>



