<!--
 * @Author: Marlon
 * @Date: 2020-02-19 10:06:12
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-25 12:24:26
 * @Description: 多列弹出框
 -->
<template>
  <el-dialog
    :title="ColDialogConfig && ColDialogConfig.title || ''"
    :visible.sync="dialogTableVisible"
    :width="(ColDialogConfig &&ColDialogConfig.width)? `${ColDialogConfig.width}px`:null"
    @close="close"
  >
    <!-- 操作行 -->
    <ul class="area-title-btn" v-if="ColDialogConfig&&ColDialogConfig.header.show">
      <li v-for="(item, index) in ColDialogConfig.header.data" :key="index">
        <el-button
          size="small"
          :type="item.classType || null"
          @click="headerBtn(item)"
        >{{item.label}}</el-button>
      </li>
    </ul>
    <!-- 多列展示层 -->
    <el-form :model="form" class="ColDialog" v-if="ColDialogConfig">
      <!-- 多段类型(默认2段) -->
      <template v-for="(item, index) in ColDialogConfig.connect.data">
        <el-form-item
          :label="(item.label && item.label.name)?`${item.label.name}：`:''"
          :label-width="item.label.width?`${item.label.width}px`:null"
          :key="index"
        >
          <ul class="area-change">
            <li v-for="(items, i) in item.list" :key="i">
              <template v-if="items.type==='select'">
                <el-select
                  v-model="form[item.modelKey]"
                  filterable
                  :placeholder="items.placeholder"
                  size="small"
                >
                  <el-option
                    v-for="(itemss, j) in items.option"
                    :key="j"
                    :label="itemss.label"
                    :value="itemss.value"
                  ></el-option>
                </el-select>
              </template>
            </li>
          </ul>
        </el-form-item>
      </template>
    </el-form>
    <div slot="footer" class="dialog-footer" v-if="ColDialogConfig && ColDialogConfig.foot.show">
      <template v-for="(item, index) in ColDialogConfig.foot.data">
        <el-button
          v-if="item.type ==='button'"
          :key="index"
          :type="item.classType || null"
          @click="submitBtn(item)"
        >{{item.label}}</el-button>
      </template>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    ColDialogVisible: {
      type: Boolean,
      default: false
    },
    ColDialogConfig: {
      type: Object,
      default: function() {
        return {
          title: "",
          header: {
            show: true,
            data: [
              {
                type: "button",
                class: "",
                label: "添加数据源",
                cb: ""
              }
            ]
          },
          connect: {
            readyOnly: false,
            data: [
              {
                label: {
                  name: "数据来源",
                  width: 120
                },
                list: [
                  {
                    type: "select",
                    defaultVal: "",
                    placeholder: "",
                    modelKey: "",
                    option: [
                      {
                        value: "选项1",
                        label: "黄金糕"
                      },
                      {
                        value: "选项2",
                        label: "双皮奶"
                      },
                      {
                        value: "选项3",
                        label: "蚵仔煎"
                      },
                      {
                        value: "选项4",
                        label: "龙须面"
                      },
                      {
                        value: "选项5",
                        label: "北京烤鸭"
                      }
                    ]
                  }
                ]
              }
            ]
          },
          foot: {
            show: true,
            data: []
          }
        };
      }
    }
  },

  data() {
    return {
      // 控制弹出框显示
      dialogTableVisible: false,
      // 表单
      form: {},
      //
      options: [
        {
          value: "选项1",
          label: "黄金糕"
        },
        {
          value: "选项2",
          label: "双皮奶"
        },
        {
          value: "选项3",
          label: "蚵仔煎"
        },
        {
          value: "选项4",
          label: "龙须面"
        },
        {
          value: "选项5",
          label: "北京烤鸭"
        }
      ],
      value: ""
    };
  },
  watch: {
    ColDialogVisible(newVal) {
      this.dialogTableVisible = newVal;
    },
    ColDialogConfig: {
      handler(newVal, oldVal) {
        // 动态生成表单字段并初始化值
        let obj_ = {};
        // if (newVal.length) {
        //   newVal.forEach(item => {
        //     if (item.key) {
        //       obj_[item.key] = item.defaultModel;
        //     }
        //   });
        //   this.form = Object.assign({}, this.form, obj_);
        //   console.log(this.form, "根据key动态创建form字段并初始化值");
        // }
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    // 提交
    submitBtn(edit) {
      if (edit.cb) {
        edit.cb(this.form);
      } else {
        console.error("按钮配置中缺少回调函数(cb)");
      }
    },
    // 关闭弹出框
    close() {
      this.$emit("editBtnCb", {
        label: "close",
        close: this.dialogTableVisible
      });
    },
    // 头部操作
    headerBtn(btnObj) {
      if (btnObj.cb) {
        btnObj.cb(this.form);
        this.dialogTableVisible = false;
      } else {
        console.error("按钮配置中缺少回调函数(cb)");
      }
    }
  }
};
</script>

<style lang="less" scoped>
// 操作行
.area {
  &-title-btn {
    overflow: hidden;
    & > li {
      float: left;
      height: 36px;
      line-height: 35px;
      margin: 10px 10px;
      padding: 0 10px;
    }
  }
  &-change {
    overflow: hidden;
    & > li {
      float: left;
      height: 41px;
      box-sizing: border-box;
      margin: 0 10px;
      &:first-child {
        margin-left: 0;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
// 表单容器
.ColDialog {
  max-height: 167px;
  overflow-x: hidden;
  overflow-y: auto;
  .input-n {
    overflow: hidden;
    display: flex;
    margin: 0 auto;
    text-align: center;
    & > li {
      float: left;
      flex: 1;
    }
  }
  .area-type {
    width: 100%;
    max-height: 126px;
    box-sizing: border-box;
    //   border: 1px solid red;
    overflow-x: hidden;
    overflow-y: auto;
    &-list {
      width: 35%;
      float: left;
    }
    &- {
      width: 10%;
      float: left;
    }
    &-icon-add {
      margin-right: 5px;
      font-size: 20px;
      cursor: pointer;
      margin-top: 10px;
      &:hover {
        color: green;
      }
    }
    &-icon-del {
      font-size: 20px;
      cursor: pointer;
      margin-top: 10px;
      &:hover {
        color: red;
      }
    }
  }
}
</style>


